'use strict';

import React, { Component, PropTypes } from 'react';
import classNames from 'classnames';
import { Util } from '../utils';

import 'ztree';
import styles from '../../node_modules/ztree/css/zTreeStyle/zTreeStyle.css';

const propTypes = {
    id: PropTypes.string,
    className: PropTypes.string,
    method: PropTypes.string,
    items: PropTypes.array,
    onClick: PropTypes.func
};

//      .
//   prop    (in  )   this.props .
const defaultProps = {
    method: 'POST', 
};

/** Class representing a Tree. */
class Tree extends Component {
    constructor(props) {
        super(props);

        // this.state = {
        //     data: [],
        //     count: props.initialCount
        // };

        this.onClick = this.onClick.bind(this);
    }

    componentWillMount() {
        //    ( )
        let id = this.props.id;
        if(typeof id === 'undefined') {
            id = Util.getUUID();
        }

        this.id = id;
    }

    componentDidMount() {
        //    ( )
        this.$tree = $('#' + this.id);
        $.fn.zTree.init(this.$tree, this.options());
    }

    componentWillReceiveProps(nextProps) {
        //   props   (    )
    }

    shouldComponentUpdate(nextProps, nextState) {
        //  props state     (    )
        // false  render  (componentWillUpdate  componentDidUpdate   )
        return false;    // default true
    }

    /**
     * @private
     */
    options() {
        const { url, method, onClick } = this.props;

        var options = {
            async: {
		        enable: true,
                type: method,
		        url: url,
		        autoParam: ["id"]
	        },
            callback: {
		        onClick: this.onClick
	        }
        };

        return options;
    }
    
    //-----------------------------
    // methods

    //-----------------------------
    // events
    onClick(e, treeId, treeNode) {
        // console.log(e);
        // console.log('treeId', treeId);
        // console.log('treeNode', treeNode);

        if(typeof this.props.onClick !== 'undefined') {
            this.props.onClick(e, treeId, treeNode);
        }
    }

    render() {
        //  
        const { className } = this.props;

        return (
            <ul id={this.id} className={classNames('ztree', className)}></ul>
        );
    }
}

Tree.propTypes = propTypes;
Tree.defaultProps = defaultProps;

export default Tree;



